import React, { Component } from 'react';
import Status from './Components/Status/Status';
import Board from './Components/Board/Board';
import Navbar from './Components/Navbar/Navbar';
import Rules from './Components/Rules/Rules';
import './App.css';

class App extends Component {

  constructor() {
    super();
    this.state = {
      player: 'X',
      squares: [],
      status: 'Next player: X',
      status2: '',
      scorex: 0,
      scorey: 0,
      route: 'home'
    }
  }

  SquareClick = (event) => {

    let squares = this.state.squares;
    const i = Number(event.target.value);
    let scorex = this.state.scorex;
    let scorey = this.state.scorey;

        if (this.GetWinner() || squares[i]) {return;}

        if (this.state.player === 'X' && !squares[i]) {
                                squares[i]='X';
                                this.setState({squares: squares, player: 'Y'});
                            
                              } else if (!squares[i]){
                                squares[i]='Y';
                                this.setState({squares: squares, player: 'X'});
                                
                              }

        if (this.GetWinner()) {
          this.setState({status: 'This round goes to ' + this.state.player, status2: 'Click here to keep playing'}); 
          if (this.state.player==='X') {
            scorex++;
            this.setState({scorex: scorex})
            if (scorex-scorey>=2) {
              this.setState({status: 'Game Over! Winner is X', status2: "Refresh to restart the game!"});}
          } else {
            scorey++;
            this.setState({scorey: scorey})
            if (scorey-scorex>=2) {
              this.setState({status: 'Game Over! Winner is Y', status2: "Refresh to restart the game!"});}
          }
        } else if (squares.length === 9 && !squares.includes(undefined)) {
            this.setState({status: 'Nobody won :(', status2: 'Click here to try again!'})
        } else if (this.state.player==='X') { 
          this.setState({status: 'Next player: Y'});
        } else {this.setState({status: 'Next player: X'});}

        
  }

  GetWinner = () => {
    const [a, b, c, d, e, f, g, h, i] = this.state.squares;
    if (
        (a===b && a===c && a) ||
        (a===e && a===i && a) ||
        (d===e && e===f && e) ||
        (g===h && h===i && g) ||
        (a===d && d===g && a) ||
        (b===e && e===h && b) ||
        (c===f && f===i && c) ||
        (c===e && e===g && c)
          ) {
      return true;
    } else {return false};
  }

  playAgain = () => {
  if (this.state.status.includes('round') || (this.state.status.includes('Nobody'))) {
    this.setState({squares: [], 
                  player: this.state.player,
                  status: 'Next player:' + this.state.player,
                  status2: ''}
                  );
  }
}

  onRouteChange = (route) => {
    this.setState({route: route});
    if (this.state.route==='rules') {
      this.setState({route: 'home'});
    }
  }

  render() {

    return (
      
          <div>
            <Navbar onRouteChange={this.onRouteChange} />  
            <div className="App">
              {(this.state.route === 'rules')
              ? <Rules/>
              : (<div></div>)
              }
              <Status status = {this.state.status}/>
              <Status status = {this.state.status2} click={this.playAgain}/>
              <Board squares={this.state.squares} click={this.SquareClick}/>
              <div className='score'>{'X: '} {this.state.scorex} {' | '} {'Y: '} {this.state.scorey} </div> 
            </div>
          </div>
        
    );
  }

}

export default App;
